import React from 'react';
import './Track.css';

/* creates and initializes a new Track component */
class Track extends React.Component {
  /* constructs a new instance of Track */
  constructor(props) {
    super(props);
    this.bind('addTrack', 'removeTrack');
  }

  /* expedites the binding of methods to 'this' */
  bind(...methods) {
    methods.forEach((method) => {
      this[method] = this[method].bind(this);
    });
  }

  /* adds a new track to props */
  addTrack() {
    this.props.onAdd(this.props.track);
  }

  /* removes a track from props */
  removeTrack() {
    this.props.onRemove(this.props.track);
  }

  /* renders relevant search results and allows the user to add or remove tracks */
  renderResults(){
    if (this.props.onAdd) return <a className='Track-action' onClick={this.addTrack}>+</a>
    else return <a className='Track-action' onClick={this.removeTrack}>-</a>
  }

  /* renders the component */
  render() {
    return (
      <div className="Track">
        <div className="Track-information">
          <h3>{this.props.track.name}</h3>
          <p>{this.props.track.artist} | {this.props.track.album}</p>
        </div>
      {this.renderResults()}
      </div>

    );
  }
}

export default Track;



