import React from 'react';
import './SearchBar.css';

  /* Creates a new instance of the SearchBar component. */
  class SearchBar extends React.Component
  {
    /* constructs a new instance of SearchBar */
    constructor(props) {
      super(props);
      this.bind('search', 'handleTermChange');
    }

    /* expedites the binding of methods to 'this' */
    bind(...methods) {
      methods.forEach((method) => { this[method] = this[method].bind(this); });
    }

    /* gives functionality to the search bar */
    search(keyword) {
      this.props.onSearch(keyword);
    }

    /* handles the change of keyword */
    handleTermChange(event) {
      this.search(event.target.value);
    }

    /* renders the component */
    render() {
      return (
        <div className="SearchBar">
          <input onChange={this.handleTermChange} prompt="Enter A Song, Album, or Artist" />
          <a>SEARCH</a>
        </div>
      );
    }
  }

export default SearchBar;



