import React from 'react';
import TrackList from '../TrackList/TrackList';
import './Playlist.css';

/* Creates and initializes a playlist component. */
class Playlist extends React.Component {
  /* constructs a new instance of Playlist */
  constructor(props) {
    super(props);
    this.handleNameChange = this.handleNameChange.bind(this);
  }

  /* Allows the user to change the playlist name. */
  handleNameChange(event) {
    this.props.onNameChange(event.target.value);
  }

  /* Renders the Playlist component. */
  render() {
    return (
      <div className="Playlist">
        <input name={this.props.name} onChange={this.handleNameChange} />
        <TrackList tracks={this.props.tracks} onRemove={this.props.onRemove} />
        <a className="Playlist-save" onClick={this.props.onSave}>SAVE TO SPOTIFY</a>
      </div>
    );
  }
}

export default Playlist;



