import React from 'react';
import Spotify from '../../util/Spotify';
import SearchBar from '../SearchBar/SearchBar';
import SearchResults from '../SearchResults/SearchResults';
import Playlist from '../Playlist/Playlist';
import './App.css';

/* gets accessToken for using the Spotify API */
Spotify.getAccessToken();

/* Creates and initializes the App class. */
class App extends React.Component {
  /* constructs a new instance of the 'App' class */
  constructor(props) {
    super(props);
    this.state = {
      searchResults: [],
      playlistName: 'Default Playlist',
      playlistTracks: []
    }
    this.bind('updatePlaylistName', 'addTrack', 'removeTrack', 'savePlaylist', 'search');
  }

  /* expedites the binding of methods to 'this' */
  bind(...methods) {
    methods.forEach((method) => { this[method] = this[method].bind(this); });
  }

  /* searches Spotify for a match to the user input */
  search(keyword) {
    Spotify.search(keyword).then(match => this.setState({searchResults: match }));
  }

  /* adds a new track to the playlist. */
  addTrack(track) {
    if (!this.state.playlistTracks.find(playlistTrack => playlistTrack.id === track.id))
    {
      this.setState(previousVal => ({ playlistTracks: [...previousVal.playlistTracks, track] }));
    }
  }

  /* removes a track via filtering from the playlist */
  removeTrack(track) {
    this.setState({playlistTracks: this.state.playlistTracks.filter(playlistTrack => playlistTrack.id !== track.id)});
  }

  /* updates the state and the value of playlistName */
  updatePlaylistName(name) {
    this.setState({ playlistName: name });
  }

  /* saves the working playlist to the user's Spotify account. */
  savePlaylist() {
    const trackUris = this.state.playlistTracks.map(playlistTrack => playlistTrack.uri);
    Spotify.savePlaylist(this.state.playlistName, trackUris);
    this.setState({ searchResults: [] });
    this.updatePlaylistName('Custom Playlist');
  }

  /* renders the application */
  render() {
    return (
      <div>
        <h1>Ja<span className="highlight">mmm</span>ing</h1>
        <div className="App">
          <SearchBar onSearch={this.search} />
          <div className="App-playlist">
            <SearchResults searchResults={this.state.searchResults} onAdd={this.addTrack} />
            <Playlist name={this.state.playlistName} tracks={this.state.playlistTracks}
              onRemove={this.removeTrack} onNameChange={this.updatePlaylistName} onSave={this.savePlaylist} />
          </div>
        </div>
      </div>
    );
  }
} // end classx

export default App;



